import React from 'react';

export default class ReadItOut extends React.Component {

    componentDidMount () {
        this.componentWillReceiveProps(this.props);
    }

    componentWillReceiveProps (nextProps){
        //We delay the setting and clearing of the accessible route transition
        //text to ensure that the screen readers don't miss it.
        const { message = '' } = this.state || {};
        const newMess = nextProps.children;
        if (message!==newMess) {
            setTimeout( () => {
                this.setState({ message: newMess });
            }, 100);
        }
    }

    //getDerivedStateFromProps (nextProps, prevState) {
    //    return ???
    //}

    render () {
        const { message = '' } = this.state || {};
        const key = 'READTHIS'; // 'RE'+String(Date.now());
        console.log('REND',message);

        return (
            <div
                aria-atomic="true"
                aria-live="polite"
                className="sr-only"
                id={key}
                key={key}
                role="status"
            >
                {message}
            </div>
        );
    }

}

/*
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
 */



